@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box}body{background-color:#283845;color:#f1ecec;font-family:'Poppins', sans-serif}.home-container{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%}.home-title{color:#f1ecec}.home-subtitle{font-weight:bold}.home-category{width:300px;height:150px;overflow:hidden;margin:.5rem;border-radius:1rem;border:2px solid #283845;box-shadow:0 0 20px 2px #24313b;transition:all 1s}.home-category:hover{cursor:pointer;border:2px solid #2dc6f5;text-decoration:none;transform:scale(1.05);opacity:1;transition:all 1s}.home-category-title{width:300px;padding:.5rem;text-align:center;background-color:rgba(1,17,29,0.705882);color:#f1ecec;border-top-left-radius:1rem;border-top-right-radius:1rem;font-weight:bold}.quiz-container{margin:auto;display:flex;flex-direction:column;justify-content:center;align-items:flex-start;width:25%}@media screen and (max-width: 680px){.quiz-container{margin:0}}.quiz-title{margin-top:.8rem;font-weight:600}.quiz-subtitle{padding-bottom:.5rem;border-bottom:3px solid rgba(172,172,172,0.733333)}.quiz-answers-container{display:flex;flex-direction:column}.admin-container{width:350px}.admin-new-category{background-color:rgba(1,17,29,0.205882);border-radius:1rem;padding:1rem;display:flex;flex-direction:column}.admin-input{caret-color:rgba(32,102,94,0.752941)}.admin-input:focus{outline:3px solid rgba(32,102,94,0.752941)}.admin-message{padding:.8rem 0;margin-top:.5rem;text-align:center}.navbar{margin:0;padding:0;background-color:rgba(1,17,29,0.205882);width:100%;display:flex;justify-content:center;align-items:center}.navbar-link:hover{text-decoration:none}.btn{background-color:rgba(1,17,29,0.705882);color:#f1ecec;font-family:'Poppins', sans-serif;font-weight:600;outline:none;border:none}.btn:active,.btn :focus{border:none;outline:none}.btn:hover{color:#f1ecec}.quiz-btn{margin:.8rem 0;padding:.8rem 8rem;border-radius:1rem}.result-btn{margin-bottom:-1rem;padding:.8rem;width:300px;border-radius:1rem}.admin-btn-category{width:30%;margin-top:.5rem;font-size:.8rem;display:flex;align-items:center;justify-content:space-around}.admin-btn-category-green{background-color:rgba(32,102,94,0.752941);width:100%;font-size:1rem}.answer{background-color:transparent;color:#f1ecec;border:1px solid rgba(172,172,172,0.733333);border-radius:1rem;padding:.8rem;width:300px;margin-bottom:.5rem;display:flex;justify-content:space-between;align-items:center}.answer:hover{outline:none;cursor:pointer;background-color:rgba(172,172,172,0.227451)}.correct{background-color:rgba(32,102,94,0.752941);border:1px solid rgba(32,102,94,0.752941)}.correct:hover,.correct :active{background-color:rgba(32,102,94,0.752941);outline:none}.incorrect{background-color:rgba(116,2,5,0.745098);border:1px solid rgba(116,2,5,0.745098)}.incorrect:hover,.incorrect :active,.incorrect :focus{background-color:rgba(116,2,5,0.745098);outline:none}.results-icons{font-size:1.5rem;font-weight:600}.results-score{display:flex;flex-direction:column;justify-content:center;align-items:center;padding:.5rem;margin-bottom:.8rem;border-radius:1rem;background-color:rgba(172,172,172,0.733333);border:2px solid transparent}

